import Vue from 'vue'
import Router from 'vue-router'

// 一级路由
import teacher from './views/teacher/teacherManager.vue'
import register from './views/register/register.vue'
import login from './views/login.vue'
import index from './views/student/index.vue'

// 二级路由
import upload from './views/teacher/upload.vue'
import student from './views/teacher/student.vue'
import modify from './views/teacher/modify.vue'
import start from './views/teacher/start.vue'
import thesis from './views/teacher/thesis.vue'
import foreign from './views/teacher/foreign.vue'
import uncomplete from './views/teacher/uncomplete.vue'
import complete from './views/teacher/complete.vue'
import allData from './views/teacher/allData.vue'
import modifyEdit from './views/teacher/modify-edit.vue'

// 二级路由
import studentManager from './views/student/studentManager.vue'
import submit from './views/student/submit.vue'
import download from './views/student/download.vue'


Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path:'/',
      redirect:'/login'
    },
    {
      path:'/login',
      name:'login',
      component:login
    },
    {
      path: '/teacher',
      name: 'teacher',
      component: teacher,
      redirect:'/teacher/upload',
      // 二级路由
      children:[
        {
          path:'/teacher/upload',
          name:'upload',
          component:upload
        },
        {
          path:'/teacher/student',
          name:'student',
          component:student
        },
        {
          path:'/teacher/modify',
          name:'modify',
          component:modify
        },
        {
          path:'/modify/edit',
          component:modifyEdit
        },
        {
          path:'/teacher/start',
          name:'start',
          component:start
        },
        {
          path:'/teacher/foreign',
          name:'foreign',
          component:foreign
        },
        {
          path:'/teacher/thesis',
          name:'thesis',
          component:thesis
        },
        {
          path:'/teacher/complete',
          name:'complete',
          component:complete
        },
        {
          path:'/teacher/uncomplete',
          name:'uncomplete',
          component:uncomplete
        },
        {
          path:'/teacher/allData',
          name:'allData',
          component:allData
        }
      ]
    },
    {
      path:'/register',
      name:'register',
      component:register
    },
    {
      path:'/student',
      name:'student',
      component:index,
      redirect:'/student/studentManager',
      children:[
        {
          path:'/student/studentManager',
          name:"studentManager",
          component:studentManager
        },
        {
          path:'/student/submit',
          name:"submit",
          component:submit
        },
        {
          path:'/student/download',
          name:"download",
          component:download
        }
      ]
    }
    
  ]
})
