<template>
    <div id="loginBox">
        <!-- 导航 -->
        <row class="loginNav">
            <i-col span="6" offset="5">
                <img src="./../assets/logo-black.png" alt="" style="margin-top:5px;">
            </i-col>
            <i-col span="13" style="height:100%;float:right;">
               <span style="float:right;margin-right:20%;line-height:60px;font-size:15px;">
                  <router-link :to="{name:'register'}">
                      <Icon type="ios-person" style="font-size:20px;margin-top:-2px;"></Icon>立即注册
                  </router-link>
               </span>
            </i-col>
        </row>
        <!-- 登录 -->
        <div id="login">
            <h2>登录</h2>
            <i-form ref="formInline" :model="formInline" :rules="ruleInline" inline>
                <Form-item prop="user" style="width:100%;">
                    <i-input type="text" v-model="formInline.user" placeholder="Username" size="large">
                        <Icon type="ios-person-outline" slot="prepend"></Icon>
                    </i-input>
                </Form-item>
                <Form-item prop="password" style="width:100%;">
                    <i-input type="password" v-model="formInline.password" placeholder="Password" size="large">
                        <Icon type="ios-recording-outline" slot="prepend"></Icon>
                    </i-input>
                </Form-item>
                <Form-item style="width:100%;">
                    <i-button type="primary" @click="handleSubmit('formInline')" long size="large">登录</i-button>
                </Form-item>
            </i-form>
        </div>

        <div class="b-footer">
            Copyright © 2019 BiXing Mo All rights reserved. More information 齐天大圣 - design by BiXing
        </div>
    </div>
</template>

<script>
export default {
    name:'login',
    data(){
        return {
            formInline: {
                    user: '',
                    password: ''
                },
            ruleInline: {
                user: [
                    { required: true, message: '请填写用户名', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请填写密码', trigger: 'blur' },
                    { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
         handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        // this.$Message.success('提交成功!');
                        this.$router.push({name:'teacher'})
                    } else {
                        this.$Message.error('用户名或密码错误!');
                    }
                })
        }
    }

}
</script>

<style scoped>
    #loginBox{
        background: -webkit-linear-gradient(top, #fff, #dadcd7);
    }
    #login{
        max-width: 500px;
        background:rgb(248, 248, 248);
        margin:223px auto;
        padding: 50px;
        border-radius: 8px;
    }
    h2{
        margin-bottom: 40px;
        font-size: 20px;
    }
    .loginNav{
        height: 60px;
        border-bottom: 1px solid rgb(216, 213, 213);
    }
    .b-footer{
        width: 100%;
        height: 70px;
        background: rgb(243, 243, 243);
        text-align: center;
        line-height: 70px;
        margin-top: 10px;
        border-top: 1px solid rgba(114, 112, 112,0.8);
        border-bottom: 1px solid rgb(114, 112, 112);
    }
    a{
        color:rgb(81, 90, 110);
    }
</style>

