<template>
    <div id="student">
     <h1>学生文件</h1>
     <i-table border :columns="columns6" :data="data5"></i-table>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                isUpload:true,
                columns6: [
                    {
                        title: '姓名',
                        key: 'name'
                    },
                    {
                        title: '班级',
                        key: 'class',
                        filters: [
                            {
                                label: '181',
                                value: 1
                            },
                            {
                                label: '182',
                                value: 2
                            },
                            {
                                label: '183',
                                value: 3
                            }
                        ],
                        filterMultiple: false,
                        filterMethod (value, row) {
                            if (value === 1) {
                                return row.class < 182;
                            } else if (value === 2) {
                                return row.class < 183 && row.class >181;
                            }else if (value === 3) {
                                return row.class < 184 && row.class >182;
                            }
                        }
                    },
                    {
                        title: '文档类型',
                        key: 'type',
                        filters: [
                            {
                                label: '开题报告',
                                value: '开题报告'
                            },
                            {
                                label: '外文翻译',
                                value: '外文翻译'
                            },
                            {
                                label: '毕业论文',
                                value: '毕业论文'
                            },
                        ],
                        filterMethod (value, row) {
                            return row.type.indexOf(value) > -1;
                        }
                    },
                    {
                        title: '日期',
                        key: 'date'
                    },
                    {
                        title: '操作',
                        key: 'controls',
                        render(row){
                            return (
                               <div>
                                <i-button onClick={(e)=>{console.log(this.row.id)}}>下载</i-button>
                                
                                <i-button style="margin-left:10px;"  type="error" onClick={(e)=>{
                                        if(confirm('您确定删除这条数据嘛?')){
                                            axios.delete("http://localhost:3000/users/"+this.row.id)
                                                         .then((response)=>{
                                                             console.log('success')
                                                         }) 
                                        }
                                    }}
                                >
                                    删除
                                </i-button>
                               </div> 
                            )
                            
                        }
                       
                    }
                ],
                data5: []
            }
        },
        methods:{
            
        },
        created(){
            axios.get("http://localhost:3000/users?status=false")
                .then((response)=>{
                    console.log(response)
                    this.data5=response.data;
                })
        },
        updated(){
            axios.get("http://localhost:3000/users?status=false")
                .then((response)=>{
                    this.data5=response.data;
                })
        }
    }
</script>

<style scoped>
    #student{
        max-width: 1200px;
        margin: 0 auto;
    }
    h1{
       margin:20px 0; 
    }
</style>



