<template>
    <div id="register">
        <!-- 导航 -->
        <row class="loginNav">
            <i-col span="6" offset="5">
                <img src="../../assets/logo-black.png" alt="" style="margin-top:5px;">
            </i-col>
            <i-col span="13" style="height:100%;float:right;">
               <span style="float:right;margin-right:20%;line-height:60px;font-size:15px;">
                  <router-link :to="{name:'login'}">
                    返回登录
                  </router-link>
               </span>
            </i-col>
        </row>
        <!-- 注册 -->
        <div class="container">
            <Tabs active-key="key1">
                <Tab-pane label="学生端注册" key="key1" class="tab">
                    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
                        <Form-item label="姓名" prop="name">
                            <i-input v-model="formValidate.name" placeholder="请输入姓名"></i-input>
                        </Form-item>
                        <Form-item label="学号" prop="no">
                            <i-input type="text" v-model="formValidate.no" number placeholder="请输入学号"></i-input>
                        </Form-item>
                        <Form-item label="班级" prop="class">
                            <i-select v-model="formValidate.class" placeholder="请选择班级">
                                <i-option value="181">181</i-option>
                                <i-option value="182">182</i-option>
                                <i-option value="183">183</i-option>
                                <i-option value="184">184</i-option>
                            </i-select>
                        </Form-item>
                        <Form-item label="邮箱" prop="mail">
                            <i-input v-model="formValidate.mail" placeholder="请输入邮箱"></i-input>
                        </Form-item>
                        <Form-item label="指导教师" prop="teacher">
                            <i-select v-model="formValidate.teacher" placeholder="请选择指导教师">
                                <i-option value="c">崔家东</i-option>
                                <i-option value="w">五月风</i-option>
                                <i-option value="a">安家东</i-option>
                            </i-select>
                        </Form-item>
                        <Form-item label="密码" prop="passwd">
                            <i-input type="password" v-model="formValidate.passwd" placeholder="请输入密码"></i-input>
                        </Form-item>
                        <Form-item label="确认密码" prop="passwdCheck">
                            <i-input type="password" v-model="formValidate.passwdCheck"  placeholder="请再次输入密码"></i-input>
                        </Form-item>
                        <Form-item>
                            <i-button type="success" @click="handleSubmit('formValidate')" style="width:80px;margin-left:30px;">提交</i-button>
                            <i-button type="primary" @click="handleReset('formValidate')" style="width:80px;margin-left:160px;">重置</i-button>
                        </Form-item>
                     </i-form>
                </Tab-pane>
                <Tab-pane label="教师端注册" key="key2">
                    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">

                        <Form-item label="姓名" prop="name">
                            <i-input v-model="formValidate.name" placeholder="请输入姓名"></i-input>
                        </Form-item>
                        <Form-item label="教师工号" prop="no">
                            <i-input type="text" v-model="formValidate.no" number placeholder="请输入教师工号"></i-input>
                        </Form-item>
                        <Form-item label="密码" prop="passwd">
                            <i-input type="password" v-model="formValidate.passwd" placeholder="请输入密码"></i-input>
                        </Form-item>
                        <Form-item label="确认密码" prop="passwdCheck">
                            <i-input type="password" v-model="formValidate.passwdCheck"  placeholder="请再次输入密码"></i-input>
                        </Form-item>

                        <i-button type="primary" class="btn">提交</i-button>
                     </i-form>
                </Tab-pane>
            </Tabs>
        </div>
        <!-- 脚部 -->
        <div class="b-footer">
            Copyright © 2019 BiXing Mo All rights reserved. More information 齐天大圣 - design by BiXing
        </div>
    </div>
</template>

<script>
// import studentRigister from 'studentRegister'

export default {
    data () {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.formValidate.passwdCheck !== '') {
                        // 对第二个密码框单独验证
                        this.$refs.formValidate.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.formValidate.passwd) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };

            return {
                formValidate: {
                    name: '',
                    mail: '',
                    teacher: '',
                    no:'',
                    passwd: '',
                    passwdCheck: ''
                },
                ruleValidate: {
                    name: [
                        { required: true, message: '姓名不能为空', trigger: 'blur' }
                    ],
                    mail: [
                        { required: true, message: '邮箱不能为空', trigger: 'blur' },
                        { type:'email', message: '邮箱格式不正确', trigger: 'blur' }
                    ],
                    teacher: [
                        { required: true, message: '请选择指导教师', trigger: 'change' }
                    ],
                    class: [
                        { required: true, message: '请选择班级', trigger: 'change' }
                    ],
                    passwd: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    passwdCheck: [
                        { validator: validatePassCheck, trigger: 'blur' }
                    ],
                    no: [
                        { required:true,type:"number",message:'请输入数字学号',trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$router.push({name:'login'});
                        console.log(555)
                    }else{
                        this.$Message.error('请将必填信息补全!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
}
</script>

<style scoped>
    .loginNav{
        height: 60px;
        border-bottom: 1px solid rgb(216, 213, 213);
    }
    .container{
        max-width: 1100px;
        height: 600px;
        margin:100px auto;
        border:1px solid rgb(238, 226, 226);
        padding: 20px;
    }
    a{
        color:rgb(81, 90, 110);
    } 
    .tab{
        padding: 20px;
    }
    form{
        width: 500px;
        margin:40px auto;
    }
    .btn{
        margin-left: 80px;
        width: 84%;
    }
    .b-footer{
        width: 100%;
        height: 70px;
        background:rgba(230, 230, 230,0.6);
        text-align: center;
        line-height: 72px;
        /* margin-top: 5px; */
    }
</style>


