<template>
    <div id="start">
        <h1>开题报告统计</h1>
        <table width="100%">
            <thead>
                <tr>
                    <th>姓名</th>
    
                    <th>学号</th>
    
                    <th>班级</th>
    
                    <th>文档类型</th>

                    <th>完成状况</th>

                    <!-- <th>备注信息</th> -->
                </tr>
            </thead>
    
            <tr v-for="data in datas">
                <td>{{data.name}}</td>
                <td>{{data.classNo}}</td>
                <td>{{data.class}}</td>
                <td>{{data.type}}</td>
                <td v-if="!data.status" style="background:rgba(255,102,0,0.7);color:white;">未完成</td>
                <td v-else style="background:rgba(0,204,102,0.7);color:white;">已完成</td>
                <!-- <td style="width:80px;">
                    <i-button size="small" type="primary">备注</i-button>
                    <i-button size="small" type="warning" style="margin:0 8px;">修改</i-button>
                    <i-button size="small" type="success">查看</i-button>
                </td> -->
            </tr>  
        </table>
    </div>

</template>

<script>
export default {
    data(){
        return {
            datas:[],
            status:"",
        }
    },
    created(){
        axios.get("http://localhost:3000/users?type=开题报告")
            .then((response)=>{
                console.log(response);
                this.datas=response.data;
            })
    }
}
</script>


<style scoped>
        table{
            border-collapse: collapse;
            text-align: left;
            text-indent: 2em;
        }
        table td, table th{
            border: 1px solid rgb(218, 217, 217);
            color: black;
            height: 43px;
        }
        table thead th{
            background-color: rgb(247, 247, 247);
            width: 80px;
        }
        #start{
            max-width:1200px;
            margin:0 auto;
        }
        h1{
            margin:20px 0; 
        }
</style>

