<template>
    <div id="update">
        <h1>修改上传</h1>
        <i-table border :columns="columns6" :data="data5"></i-table>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                columns6: [
                    {
                        title: '姓名',
                        key: 'name'
                    },
                    {
                        title: '班级',
                        key: 'class',
                        filters: [
                            {
                                label: '181',
                                value: 1
                            },
                            {
                                label: '182',
                                value: 2
                            },
                            {
                                label:'183',
                                value:3
                            }
                        ],
                        filterMultiple: false,
                        filterMethod (value, row) {
                            if (value === 1) {
                                return row.class < 182;
                            } else if (value === 2) {
                                return row.class < 183 && row.class >181;
                            }else if (value === 3) {
                                return row.class < 184 && row.class >182;
                            }
                        }
                    },
                    {
                        title: '文档类型',
                        key: 'type',
                        filters: [
                            {
                                label: '开题报告',
                                value: '开题报告'
                            },
                            {
                                label: '外文翻译',
                                value: '外文翻译'
                            },
                            {
                                label: '毕业论文',
                                value: '毕业论文'
                            },
                        ],
                        filterMethod (value, row) {
                            return row.type.indexOf(value) > -1;
                        }
                    },
                    {
                        title: '日期',
                        key: 'date'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        render (row, column, index) {
                            return (
                                <div>
                                    <router-link to="/modify/edit">
                                        <i-button type="primary">修改上传</i-button>
                                    </router-link>
        
                                     <i-button type="success" style="margin-left:20px;" onclick={(e)=>{
                                         axios.put("http://localhost:3000/users/"+this.row.id,{
                                             name:this.row.name,
                                             class:this.row.class,
                                             classNo:this.row.classNo,
                                             type:this.row.type,
                                             date:this.row.date,
                                             status:true
                                         })
                                              .then((response)=>{
                                                console.log(555)
                                              })
                                     }}>
                                        审批通过
                                    </i-button>  
                                </div>
                            )
                        }
                       
                    }
                ],
                data5: []
            }
        },
        methods:{
            
        },
        created(){
            axios.get("http://localhost:3000/users?status=false")
                .then((response)=>{
                    console.log(response);
                    this.data5=response.data;
                })
        },
        updated(){
            axios.get('http://localhost:3000/users?status=false')
                .then((response)=>{
                    this.data5=response.data;
                })
        }
    }

    
</script>

<style scoped>
    #update{
        max-width: 1200px;
        margin:0 auto;
    }
    h1{
        margin:20px 0;
    }
</style>



