<template>
    <div id="complete">
        <h1>整体统计</h1>
         <i-input placeholder="请输入要查看的学生姓名" v-model="search" style="margin-bottom:20px;"></i-input>
        <table width="100%">
            <thead>
                <tr>
                    <th>姓名</th>
    
                    <th>学号</th>
    
                    <th>班级</th>
    
                    <th>文档类型</th>

                    <th>完成状况</th>
                </tr>
            </thead>
    
            <tr v-for="data in filteredUsers">
                <td>{{data.name}}</td>
                <td>{{data.classNo}}</td>
                <td>{{data.class}}</td>
                <td>{{data.type}}</td>
                <td v-if="!data.status" style="background:rgba(255,102,0,0.7);color:white;">未完成</td>
                <td v-else style="background:rgba(0,204,102,0.7);color:white;">已完成</td>
            </tr>  
        </table>
    </div>

</template>

<script>
export default {
    data(){
        return {
            datas:[],
            status:"",
            search:""
        }
    },
    created(){
        axios.get("http://localhost:3000/users")
            .then((response)=>{
                console.log(response);
                this.datas=response.data;
            })
    },
    computed:{
			// 匹配搜索
			filteredUsers:function(){
				return this.datas.filter((users)=>{
					return users.name.match(this.search);
				})
			}
	},
}
</script>


<style scoped>
        table{
            border-collapse: collapse;
            text-align: left;
            text-indent: 2em;
        }
        table td, table th{
            border: 1px solid rgb(218, 217, 217);
            color: black;
            height: 43px;
        }
        table thead th{
            background-color: rgb(247, 247, 247);
            width: 80px;
        }
        #complete{
            max-width:1200px;
            margin:0 auto;
        }
        h1{
            margin:20px 0; 
        }
</style>

